.button {
  padding: 0.8rem 2rem;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s;
  text-align:  center;
}

.button:hover {
  opacity: 0.8;
}

.button.active {
  background: #3e6ae1;
  color: #fff;
}

.button.inactive {
  background:    #dfdfdf;
  color: black;
}

.button:disabled {
  cursor: not-allowed;
}

